<template>

  <kirby-view class="kirby-user-view">

    <kirby-header icon="users" label="User List" link="/users" :breadcrumb="breadcrumb" :pagination="pagination" @prev="prev" @next="next">

      <kirby-fancy-input
        class="kirby-user-view-name"
        placeholder="Enter a name …"
        tag="div"
        type="headline"
        :key="user.id + '-headline'"
        :value="user.content.name"
        @blur="updateName($event.target.innerText)"
        @enter="$event.target.blur()" />

      <kirby-button class="kirby-user-view-image" v-if="image" @click="$refs.upload.open()">
        <kirby-image :cover="true" ratio="1/1" :src="image" />
      </kirby-button>

      <template slot="buttons-left">
        <kirby-dropdown>
          <kirby-button @click="$refs.picture.toggle()" icon="image">
            {{ $t('user.image') }}
          </kirby-button>
          <kirby-dropdown-content ref="picture" :dark="true">
            <template v-if="image">
              <kirby-dropdown-item icon="upload" @click="$refs.upload.open()">
                {{ $t('change') }}
              </kirby-dropdown-item>
              <kirby-dropdown-item icon="trash" @click="action('picture.delete')">
                {{ $t('delete') }}
              </kirby-dropdown-item>
            </template>
            <template v-else>
              <kirby-dropdown-item icon="upload" @click="$refs.upload.open()">
                {{ $t('upload') }}
              </kirby-dropdown-item>
              <kirby-dropdown-item icon="import" @click="action('picture.import')">
                {{ $t('import') }}
              </kirby-dropdown-item>
            </template>
          </kirby-dropdown-content>
        </kirby-dropdown>
        <kirby-button icon="bolt" @click="action('role')">
          {{ $t('user.role') }}
        </kirby-button>
        <kirby-button icon="key" @click="action('password')">
          {{ $t('password') }}
        </kirby-button>
        <kirby-button icon="trash" @click="action('remove')">
          {{ $t('delete') }}
        </kirby-button>
      </template>

    </kirby-header>

    <kirby-form
      :fields="fields"
      :values="user.content"
      @submit="save"
      @input="input" />

    <kirby-user-role-dialog ref="role" @success="fetch" />
    <kirby-user-password-dialog ref="password" />
    <kirby-user-remove-dialog ref="remove" />

    <kirby-upload ref="upload" :url="uploadApi" accept="image/jpeg" :multiple="false" @success="fetch" />

  </kirby-view>

</template>

<script>

export default {
  props: {
    id: {
      type: String
    }
  },
  data () {
    return {
      user: {
        content: {
          language: 'en',
        },
        prev: null,
        next: null,
        image: {
          url: null,
          modified: null
        }
      },
      image: null,
      breadcrumb: null
    }
  },
  computed: {
    fields() {
      return [
        {
          name: 'email',
          label: this.$t('email'),
          type: 'email',
          placeholder: 'mail@example.com',
          readonly: true
        },
        {
          name: 'website',
          label: this.$t('website'),
          type: 'url'
        },
        {
          name: 'language',
          label: this.$t('user.language'),
          type: 'language'
        }
      ]
    },
    uploadApi () {
      return window.panel.config.api + '/users/' + this.user.id + '/avatar';
    },
    pagination () {
      return {
        prevLabel: this.$t('user.previous'),
        prev: this.user.prev ? true : false,
        next: this.user.next ? true : false,
        nextLabel: this.$t('user.next')
      };
    }
  },
  created () {
    this.fetch();
    this.$events.$on('key.save', this.save);
  },
  destroyed: function () {
    this.$events.$off('key.save', this.save);
  },
  watch: {
    $route () {
      this.fetch();
    }
  },
  methods: {
    input (data) {

      if (data.language === this.user.language) {
        return true;
      }

      // if current panel user, switch language
      if(data.language && this.$store.state.user.id === this.user.id) {
        this.user.language = data.language;
        this.$store.dispatch('language', data.language);
      }

    },
    save () {
      this.$api.user.update(this.id, this.user.content).then(() => {
        this.$store.dispatch('success', 'Saved!');
      }).catch((error) => {
        this.$store.dispatch('error', error.message);
      });
  },
    action (action) {
      switch (action) {
        case 'picture.delete':
          this.$api.avatar.delete(this.id).then(() => {
            this.$store.dispatch('success', this.$t('notification.image.deleted'));
            this.image = null;
          }).catch((error) => {
            this.$store.dispatch('error', error.message);
          });
          break;
        case 'role':
          this.$refs.role.open(this.user.id);
          break;
        case 'password':
          this.$refs.password.open(this.user.id);
          break;
        case 'remove':
          this.$refs.remove.open(this.user.id);
          break;
        default:
          this.$store.dispatch('error', 'Not yet implemented');
          break;
      }
    },
    prev () {
      this.$router.push('/users/' + this.user.prev);
    },
    next () {
      this.$router.push('/users/' + this.user.next);
    },
    fetch () {
      this.$api.user.get(this.id).then((user) => {

        this.user       = user;
        this.values     = user.content;
        this.breadcrumb = this.$api.user.breadcrumb(user);

        if (user.image.exists) {
          this.image = user.image.url + '?v=' + user.image.modified;
        } else {
          this.image = null;
        }

        this.$store.dispatch('isLoading', false);

      }).catch(() => {
        this.$store.dispatch('error', 'The user could not be found');
        this.$router.push('/users');
      });
    },
    updateName (name) {

      if (name === this.user.content.name) {
        return true;
      }

      this.$api.user.update(this.id, {name: name}).then((user) => {
        this.user = user;
        this.$store.dispatch('success', 'The name has been saved!');
      }).catch((error) => {
        this.$store.dispatch('error', error.message);
      });

    }
  }
}

</script>

<style lang="scss">

.kirby-headline {
  position: relative;
  padding-right: 5rem;
}
.kirby-user-view-image {
  position: absolute;
  top: 50%;
  width: 3rem;
  border-radius: 50%;
  margin-top: -1.5rem;
  overflow: hidden;
  z-index: 0;

  [dir="ltr"] & {
    right: .6rem;
    margin-right: -.2rem;
  }
  [dir="rtl"] & {
    left: .6rem;
    margin-left: -.2rem;
  }
}

</style>



